"use client"
import { useEffect, useState } from "react";

export default function LandingPage() {
  const [showFullName, setShowFullName] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowFullName(true);
    }, 2000);
    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="min-h-screen flex flex-col justify-center items-center bg-gray-50">
      <h1 className={`font-bold text-blue-600 transition-all duration-500 ${
        showFullName ? "text-4xl" : "text-6xl"
      }`}>
        RISE
      </h1>
      {showFullName && (
        <h2 className={`mt-4 font-normal text-gray-600 text-xl transition-opacity duration-1000 ${
          showFullName ? "opacity-100" : "opacity-0"
        }`}>
          Recursion Iteration Solution Efficiently 
        </h2>
      )}
    </div>
  );
}